<template>
	<div class = "overlay__inner_wrapper">

		<div class = "overlay__body">
			
			<div class = "comparison__selected_item">
				<img alt = "comparison image" :src = "metaData.path + selectedImage">
			</div>

			<div class="overlay__controls">
				<button v-on:click="swapImage($event, true)" :class = "(selectedButton == 1) ? 'selected' : 'normal'">Redux</button>
				<div class = "border"></div>
				<button v-on:click="swapImage($event, false)" :class = "(selectedButton == 2) ? 'selected' : 'normal'">Original</button>
			</div>
		</div>

	</div>
</template>

<script>
	export default {
		name: "ComparisonOverlay",

		props: ["imgData", "metaData"],

		data: function () {
			return {
				selectedImage: this.imgData.redux,

				selectedButton: 1

			}
		},

		methods:  {

			swapImage: function (e, imgToDisplay) {
				
				e.stopPropagation();
				console.log('test');

				this.selectedImage = (imgToDisplay == true) ? this.imgData.redux : this.imgData.old;

				this.selectedButton = (imgToDisplay == true) ? 1 : 2;

			}
		}
	};
</script>